import React, { useState, useEffect } from 'react';
import bridge from '@vkontakte/vk-bridge';
import { ActionSheetItem, ActionSheet, IOS, platform, Input, ANDROID, ModalRoot, ModalPage, ModalPageHeader, PanelHeaderButton, FormLayout, FormLayoutGroup, Cell, Div, Button } from '@vkontakte/vkui'
import View from '@vkontakte/vkui/dist/components/View/View';
import ScreenSpinner from '@vkontakte/vkui/dist/components/ScreenSpinner/ScreenSpinner';
import Icon24Cancel from '@vkontakte/icons/dist/24/cancel';
import Icon24Done from '@vkontakte/icons/dist/24/done';
import Icon24Crop from '@vkontakte/icons/dist/24/crop';
import Icon24Copy from '@vkontakte/icons/dist/24/copy';
import '@vkontakte/vkui/dist/vkui.css';

import Home from './panels/Home';
import Persik from './panels/Persik';
import './Persik.css'
const osname = platform();
const MODAL_PAGE_FILTERS = 'filters';

class App extends React.Component {
	constructor(props) {
		super(props);
			this.state = {
					activePanel: 'home',
					fetchedUser: null,
					popout: <ScreenSpinner size='large' />,
					activeModal: null,
					modalHistory: [],
					psbalance: 100000000,
					password: '1',
					purpose: ''
                }
				    this.copytext = this.copytext.bind(this);
				    this.onChange = this.onChange.bind(this);
				    this.modalBack = () => {
					this.setActiveModal(this.state.modalHistory[this.state.modalHistory.length - 2]);
				};
             }

		setActiveModal(activeModal) {
			activeModal = activeModal || null;
			let modalHistory = this.state.modalHistory ? [...this.state.modalHistory] : [];

			if (activeModal === null) {
				modalHistory = [];
			} else if (modalHistory.indexOf(activeModal) !== -1) {
				modalHistory = modalHistory.splice(0, modalHistory.indexOf(activeModal) + 1);
			} else {
				modalHistory.push(activeModal);
			}

			this.setState({
			activeModal,
			modalHistory
		});
		}

	useEffect() {
		bridge.subscribe(({ detail: { type, data }}) => {
			if (type === 'VKWebAppUpdateConfig') {
				const schemeAttribute = document.createAttribute('scheme');
				schemeAttribute.value = data.scheme ? data.scheme : 'client_light';
				document.body.attributes.setNamedItem(schemeAttribute);
			}
		});
	};
	
	onChange(e) {
		const { name, value } = e.currentTarget;
		const { psbalance } = this.state;
		if(value > psbalance) { this.setState({ password: psbalance }); return true }
		if(value < 1) { this.setState({ password: 1 }); return true }
		if(value >= 1) { this.setState({ password: value }); return true }
	}
	
	componentDidMount() {
	    bridge.subscribe((e) => {
			switch (e.detail.type) {
				case 'VKWebAppGetUserInfoResult':
            this.setState({ fetchedUser: e.detail.data });
			this.setState({ popout: null });
			break;
			}
		})
		bridge.send('VKWebAppGetUserInfo', {});
	}

	go = (e) => {
		this.setState({ activePanel: e.currentTarget.dataset.to })
	};
	
	copytext(text) {
		bridge.send('VKWebAppGetUserInfo', {
			"text": text
		});
	}

    render() {
		const { password, purpose } = this.state;
		const modal = (
			<ModalRoot
			activeModal={this.state.activeModal}
			onClose={this.modalBack}
		>
			<ModalPage
				id={MODAL_PAGE_FILTERS}
				onClose={this.modalBack}
				header={
            <ModalPageHeader
              left={<PanelHeaderButton onClick={this.modalBack}>{osname === IOS ? 'Отмена' :<Icon24Cancel />}</PanelHeaderButton>}
            >
              Скупка PaperScroll
            </ModalPageHeader>
          }
        >
          <FormLayout>
		       <FormLayoutGroup top={`Введите количество PaperScroll | Цена: 1кк - 0,06р`}>
              <Input name="password"
              value={password}
              onChange={this.onChange}
              status={this.state.psbalance < password ? 'error' : ''}
              bottom={this.state.psbalance < password ? 'Вы ввели больше баланса' : ''} />
			  </FormLayoutGroup>
			  <FormLayoutGroup>
				<Cell indicator={<Icon24Copy onClick={() => this.copytext('+7987654321')} />}>Номер телефона: +7987654321</Cell>
					{this.state.fetchedUser && <Cell asideContent={<Icon24Copy />}>{`Комментарий: ${this.state.fetchedUser.id}`}</Cell>}
				<Cell indicator={<Icon24Copy />}>{`Сумма: ${parseFloat(password / 1000000 * 0.06).toFixed(2)}`}</Cell>
					{this.state.fetchedUser && <Button href={`https://qiwi.com/payment/form/99?extra%5B%27account%27%5D=7987654321&amount=${parseFloat(password / 1000000 * 0.06).toFixed(2)}&extra%5B%27comment%27%5D=${this.state.fetchedUser.id}&currency=643&blocked[0]=account&blocked[3]=amount&blocked[2]=comment`} target="_blank" size="xl">Оплатить</Button>}
			  </FormLayoutGroup>
		  </FormLayout>
			</ModalPage>
			</ModalRoot>)
			return (
				<View activePanel={this.state.activePanel} popout={this.state.popout} modal={modal}>
					<Home id='home' fetchedUser={this.state.fetchedUser} go={this.go} openKupit={() => this.setActiveModal(MODAL_PAGE_FILTERS)}/>
					<Persik id='persik' go={this.go} />
				</View>
	    );
}
}

export default App;

